<template>
  <div class="header">
    <h1>Projects</h1>
    <ul id="projects-list">
      <li v-for="(category, index) in categories" :key="index">{{ category }}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "ProjectsList",
  data() {
    return {
      categories: ["React", "Vue", "Node", "Golang", "PHP"],
    };
  },
};
</script>
<style scooped>
h1 {
  font-family: fantasy;
}
li {
  font-size: 24px;
}
#projects-list {
  text-align: left;
}
</style>



