    <iframe style="height: 80vh; width: 50vw;" src="https://www.nintenderos.com"></iframe>
  </div>
</template>

<script>
export default {
  name: "MainPage",
  data() {
    return {
      msg: [
        "little Hobbit",
        "Lord Saruman",
        "my God of Destruction",
        "my little sacrifice",
      ],
    };
  },
};
</script>
<style scooped>
h1 {
  font-family: fantasy;
  font-size: 1.5rem;
}
</style>



