import Vue from 'vue'
import Router from 'vue-router'
import MainPage from '@/components/Main'
import ProjectsList from '@/components/Projects'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component: MainPage
    },
    {
      path: '/projects',
      name: 'projects-list',
      component: ProjectsList
    }
  ]
})



